import React, { PureComponent } from 'react';
import { connect } from 'react-redux';

import { STORE_KEY } from '../../constants';
import { selectAnnotation } from '../../actions';
import { getVisibleAnnotationsWithCoordinates } from '../../selectors';

class Annotation extends PureComponent {
  render() {
    const {
      startX,
      width,
      cssClass = 'defaultAnnotation',
      onClick,
    } = this.props;

    return (
      <div
        className={'annotation ' + cssClass}
        style={{
          transform: `translateX(${startX}px)`,
          width,
        }}
        onClick={onClick}
      />
    );
  }
}

class Annotations extends PureComponent {
  render() {
    const { visibleAnnotationsWithCoordinates } = this.props;

    return (
      <div className="annotations">
        {visibleAnnotationsWithCoordinates.map(annotationWithCoordinates => {
          const { annotation } = annotationWithCoordinates;
          return (
            <Annotation
              key={annotation.id}
              startX={annotationWithCoordinates.startX}
              width={annotationWithCoordinates.width}
              cssClass={annotation.cssClass}
              onClick={() =>
                this.props.dispatch(selectAnnotation(annotation.id))
              }
            />
          );
        })}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  visibleAnnotationsWithCoordinates: getVisibleAnnotationsWithCoordinates(
    state
  ),
});

export default connect(mapStateToProps, undefined, undefined, {
  storeKey: STORE_KEY,
})(Annotations);
